import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/sign',
      name: 'sign',
      redirect: { name: 'login' },
      component: () => import('./views/photo/user/sign'),
      children: [
        {
          path: '/login',
          name: 'login',
          component: () => import('./views/photo/user/sign/login')
        },
        {
          path: '/register',
          name: 'register',
          component: () => import('./views/photo/user/sign/register')
        },
        {
          path: '/forget',
          name: 'forget',
          component: () => import('./views/photo/user/sign/forget')
        }
      ]
    },
    {
      path: '/',
      name: 'layout',
      redirect: { name: 'home' },
      component: () => import('./components/layout'),
      children: [
        {
          path: '/home',
          name: 'home',
          component: () => import('./views/photo/home')
        },
        {
          path: '/search',
          name: 'search',
          component: () => import('./views/photo/search')
        },
        {
          path: '/notice',
          name: 'notice',
          component: () => import('./views/photo/notice')
        },
        {
          path: '/user',
          name: 'user',
          component: () => import('./views/photo/user')
        }
      ]
    },
    {
      path: '/createproject',
      name: 'createproject',
      component: () => import('./views/photo/project/createProject')
    },
    {
      path: '/projectdetail',
      name: 'projectdetail',
      component: () => import('./views/photo/project')
    }
  ]
})
