import Cookies from './cookie'

const TokenKey = 'SM_USER_ID'

const token = {
  getToken() {
    return Cookies.getCookie(TokenKey)
  },
  setToken(token) {
    return Cookies.setCookie(TokenKey, token, 7)
  },
  removeToken() {
    return Cookies.remove(TokenKey)
  }
}
export default token
