const cookie = {
  // 存储cookie
  setCookie(name, value, day) {
    let expires = new Date()
    expires.setTime(expires.getTime() + 1000 * 60 * 60 * 24 * day)
    window.document.cookie = `${name}=${value};expires=${expires}`
  },
  // 获取cookie
  getCookie(name) {
    let cookies = window.document.cookie.split(';')
    let value = ''
    cookies.some(item => {
      let keyList = item.split('=')
      value = name === keyList[0] ? keyList[1] : ''
      return value
    })
    return value
  }
}
export default cookie
